import React, {lazy, useEffect, useState} from 'react'
import {
  CBadge,
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CCollapse,
  CDataTable,
  CFormGroup,
  CInput,
  CInputCheckbox,
  CLabel,
  CModal,
  CModalHeader,
  CRow,
  CSelect,
} from '@coreui/react'
import {NotificationManager} from 'react-notifications';
import {useStore} from '../../MobxStore'
import {
  createUsers,
  deleteUser,
  loadRoles,
  loadUsers,
  reactivateUser,
  suspendUser
} from '../../services/userManagementService';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';


const getBadge = status => {
  switch (status) {
    case 1:
      return 'success';
    case 'Inactive':
      return 'secondary';
    case 2:
      return 'warning';
    case 4:
      return 'danger';
    case 5:
      return 'danger';
    default:
      return 'primary'
  }
};

const WidgetsDropdown = lazy(() => import('../widgets/WidgetsDropdown.js'));
const WidgetsBrand = lazy(() => import('../widgets/WidgetsBrand.js'));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});


const Users = () => {


  const [modal, setModal] = useState(false);


  const [alterAction, setAlterAction] = useState('');
  const [dialogConfirm, setDialogConfirm] = React.useState(false);
  const [userTobeAltered, setUserTobeAltered] = useState({});
  const [columns, setColumns]= useState([]);


  const toggle = () => {

    setModal(!modal);
    setFirstName("");
    setLastName("");
    setEmail("");
    setPhoneNumber("");
    setUserRoleId("");
    setEmailSelected(true);
    setSmsSelected(false);
  };




  const store = useStore();

  useEffect(() => {
    const fields = [];
    fields.push("firstName",
      "lastName",
      {key: 'email', _style: {width: '20%'}},
      "phone",
      {
        key: 'userName',
        label: 'userName',
        sorter: false,
        filter: false
      },
      "role",
      'status',
      {
        key: 'show_details',
        label: 'Details',
        _style: {width: '1%'},
        sorter: false,
        filter: false
      });
    if(store.login.roles.includes("VIEW_AND_MAINTAIN_USER_MANAGEMENT")){
      fields.push({
          key: 'update',
          label: 'Update',
          sorter: false,
          filter: false,
        },
        {
          key: 'delete',
          label: 'Delete',
          sorter: false,
          filter: false
        })
    }
    setColumns(fields);
  }, []);


  const handleSubmit = (event) => {
    event.preventDefault();

    console.log("handle Submit here");
    const notTypes = [];
    if (emalSelected) {
      notTypes.push("email");
    }
    if (smsSelected) {
      notTypes.push("sms")
    }

    const insertDto = {
      "userRoleId": userRoleId,
      "firstName": firstName,
      "lastName": lastName,
      "email": email,
      "phone": phoneNumber,
    };

    console.log(insertDto);
    createUsers(store.login.token, insertDto).then(response => {
      if (response.status) {
        NotificationManager.success(response.message, 'Success', 3000);
        load_users();
        toggle();

      } else {
        NotificationManager.error(response.message, 'Failed!', 3000);
      }
    })
      .catch(err => {
        console.log(err);
        if (err.response.status == 403) {
          NotificationManager.error("You are not allowed to perform this task", 'Failed!', 3000);
        } else {
          NotificationManager.error("Unable to try User Create, check your connection", 'Failed!', 3000);
        }
      });

  };

  const confirmDialog = (user, action) => {
    setDialogConfirm(true);
    setUserTobeAltered(user);
    setAlterAction(action);
  };

  const handleAction = (id) => {

    if (alterAction.length > 0) {
      if (alterAction == "Delete") {
        handleUserDelete(id);
      } else if (alterAction == "Suspend") {
        handleUserSuspend(id);
      } else if (alterAction == "Reactivate") {
        handleUserReactivate(id);
      }

    }
  };

  const handleUserDelete = (id) => {

    deleteUser(store.login.token, id).then(response => {
      if (response.status) {
        NotificationManager.success(response.message, 'Success', 3000);
        load_users();
        setDialogConfirm(false);
        toggleDetails([]);
        // toggle();

      } else {
        NotificationManager.error(response.message, 'Failed!', 3000);
      }
    })
      .catch(err => {
        console.log(err);
        if (err.response.status == 403) {
          NotificationManager.error("You are not allowed to perform this task", 'Failed!', 3000);
        } else {
          NotificationManager.error("Unable to try User Delete, check your connection", 'Failed!', 3000);
        }
      });

  };

  const handleUserSuspend = (id) => {

    suspendUser(store.login.token, id).then(response => {
      if (response.status) {
        NotificationManager.success(response.message, 'Success', 3000);
        load_users();
        setDialogConfirm(false);
        toggleDetails([]);

      } else {
        NotificationManager.error(response.message, 'Failed!', 3000);
      }
    })
      .catch(err => {
        console.log(err);
        if (err.response.status == 403) {
          NotificationManager.error("You are not allowed to perform this task", 'Failed!', 3000);
        } else {
          NotificationManager.error("Unable to try User Suspend, check your connection", 'Failed!', 3000);
        }
      });

  };

  const handleUserReactivate = (id) => {

    reactivateUser(store.login.token, id).then(response => {
      if (response.status) {
        NotificationManager.success(response.message, 'Success', 3000);
        load_users();
        setDialogConfirm(false);
        toggleDetails([]);

      } else {
        NotificationManager.error(response.message, 'Failed!', 3000);
      }
    })
      .catch(err => {
        console.log(err);
        if (err.response.status == 403) {
          NotificationManager.error("You are not allowed to perform this task", 'Failed!', 3000);
        } else {
          NotificationManager.error("Unable to try User Suspend, check your connection", 'Failed!', 3000);
        }
      });

  };


  const [countries, setCountries] = useState([]);
  const [userRoles, setUserRoles] = useState([]);
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [userRoleId, setUserRoleId] = useState('');
  const [smsSelected, setSmsSelected] = useState(false);
  const [emalSelected, setEmailSelected] = useState(true);


  const load_users = () => {
    loadUsers(store.login.token).then(response => {

      setCountries(response);
      console.log(response);

    })
      .catch(err => console.log(err));
  };



  useEffect(() => {
    load_users()
  }, []);

  useEffect(() => {
    loadRoles(store.login.token).then(response => {
      setUserRoles(response);
      console.log(response);
    }).catch(err => console.log(err));
  }, []);


  const [details, setDetails] = useState([]);
  // const [items, setItems] = useState(usersData)

  const toggleDetails = (index) => {
    const position = details.indexOf(index);
    let newDetails = details.slice();
    if (position !== -1) {
      newDetails.splice(position, 1)
    } else {
      newDetails = [...details, index]
    }
    setDetails(newDetails)
  };

  return (
    <>
      <CModal
        show={modal}
        onClose={toggle}
        size='lg'
        backdrop
        closeOnBackdrop={false}
      >
        <CModalHeader size="modal-lg" closeButton><h4>Add User</h4></CModalHeader>

        <CRow>
          <CCol xs="12" md="12">
            <CCard>
              <CCardBody>
                <form onSubmit={handleSubmit}>

                  <CFormGroup row>
                    <CCol md="2">
                      <CLabel htmlFor="text-input">First Name</CLabel>
                    </CCol>
                    <CCol xs="12" md="4">
                      <CInput id="text-input" name="text-input" onInput={e => setFirstName(e.target.value)}
                              value={firstName} placeholder="Enter First Name" required/>
                      {/* <CFormText>This is a help text</CFormText> */}
                    </CCol>
                    <CCol md="2">
                      <CLabel htmlFor="text-input">Last Name</CLabel>
                    </CCol>
                    <CCol xs="12" md="4">
                      <CInput id="text-input" name="text-input" onInput={e => setLastName(e.target.value)}
                              value={lastName} placeholder="Enter Last Name" required/>
                      {/* <CFormText>This is a help text</CFormText> */}
                    </CCol>
                  </CFormGroup>
                  <CFormGroup row>
                    <CCol md="2">
                      <CLabel htmlFor="email-input">Phone Number</CLabel>
                    </CCol>
                    <CCol xs="12" md="4">
                      <CInput id="email-input" name="email-input" onInput={e => setPhoneNumber(e.target.value)}
                              value={phoneNumber} placeholder="Enter Phone Number" required/>
                      {/* <CFormText className="help-block">Please enter your email</CFormText> */}
                    </CCol>
                    <CCol md="2">
                      <CLabel htmlFor="email-input">Email</CLabel>
                    </CCol>
                    <CCol xs="12" md="4">
                      <CInput type="email" id="email-input" name="email-input" onInput={e => setEmail(e.target.value)}
                              value={email} placeholder="Enter Email" autoComplete="email" required/>
                      {/* <CFormText className="help-block">Please enter your email</CFormText> */}
                    </CCol>
                  </CFormGroup>
                  <CFormGroup row>

                  </CFormGroup>
                  <CFormGroup row>
                    <CCol md="2">
                      <CLabel htmlFor="selectLg">Select Role</CLabel>
                    </CCol>
                    <CCol xs="12" md="4" size="lg">
                      <CSelect custom name="selectLg" id="selectLg"
                               value={userRoleId}
                               onChange={e => {
                                 setUserRoleId(e.target.value)
                               }}>
                        <option key={-1} value={0}>--Select User Role--</option>
                        {userRoles.map((item, i) => {
                          return (
                            <option key={i} value={item.id}>{item.name}</option>
                          )
                        }, this)}
                      </CSelect>
                    </CCol>

                    <CCol tag="label" sm="2" md="2" className="col-form-label">
                      Notification
                    </CCol>
                    <CCol sm="4">
                      <CFormGroup variant="custom-checkbox" inline>
                        <CInputCheckbox
                          custom
                          id="inline-checkbox1"
                          name="inline-checkbox1"
                          value="sms"
                          disabled
                          checked={smsSelected}
                          onClick={(e) => {
                            setSmsSelected(e.target.checked);
                            console.log("Sms selected" + e.target.checked)
                          }}
                        />
                        <CLabel variant="custom-checkbox" htmlFor="inline-checkbox1">SMS</CLabel>

                        <CFormGroup variant="custom-checkbox" inline/>

                      </CFormGroup>
                      <CFormGroup variant="custom-checkbox" inline>
                        <CInputCheckbox
                          custom id="inline-checkbox2"
                          name="inline-checkbox2"
                          value="email"
                          checked={emalSelected}
                          onClick={(e) => {
                            setEmailSelected(e.target.checked);
                            console.log("Email selected" + e.target.checked)
                          }}/>
                        <CLabel variant="custom-checkbox" htmlFor="inline-checkbox2">Email</CLabel>
                      </CFormGroup>
                    </CCol>
                  </CFormGroup>
                  <CCardFooter/>
                  <div className="d-flex justify-content-between">
                    <button type="button" className="btn btn-outline-danger m-2 btn-md" onClick={toggle}>Cancel</button>

                    <button type="submit" className="btn btn-outline-success m-2 btn-md">Submit</button>
                  </div>
                </form>
              </CCardBody>
            </CCard>
          </CCol>
          <CCol xs="12" md="6">
          </CCol>
        </CRow>


      </CModal>


      <CRow>
        <CCol>
          <CCard>
            <CCardHeader>

              <div className="d-flex justify-content-between">
                <h4>Users</h4>
                <div className="d-flex justify-content-end">
                  {store.login.roles.includes("CREATE_USER")?
                  <button type="button" className="btn btn-outline-success m-2 btn-md" onClick={toggle}>Add User
                  </button>
                    :""}
                </div>
              </div>
            </CCardHeader>
            <CCardBody>

              <CDataTable
                items={countries}
                fields={columns}
                tableFilter
                itemsPerPageSelect
                itemsPerPage={5}
                hover
                size="sm"
                striped
                bordered
                outlined
                pagination
                scopedSlots={{
                  'status':
                    (item) => (
                      <td>
                        <CBadge color={getBadge(item.statusId)}>
                          {item.statusName}
                        </CBadge>
                      </td>
                    ),
                  'role':
                    (item) => (
                      <td>
                        {item.userRoleName}
                      </td>
                    ),
                  'userName':
                    (item) => (
                      <td>
                        {item.userName == null ? <strong>Not Set</strong> : item.userName}
                      </td>
                    ),

                  'show_details':
                    (item, index) => {
                      return (
                        <td className="py-2">
                          <CButton
                            color="primary"
                            variant="outline"
                            shape="square"
                            size="sm"
                            onClick={() => {
                              toggleDetails(index)
                            }}
                          >
                            {details.includes(index) ? 'Hide' : 'Show'}
                          </CButton>
                        </td>
                      )
                    },

                  'update':
                    (item, index) => {
                      return (
                        <div>{store.login.roles.includes("VIEW_AND_MAINTAIN_USER_MANAGEMENT")?
                          <td className="py-2">

                              <CButton
                                color="warning"
                                variant="outline"
                                shape="square"
                                size="sm"
                                onClick={() => {
                                }}
                              >
                                Update
                              </CButton>
                          </td>:""}
                        </div>

                      )
                    },
                  'delete':
                    (item, index) => {
                      return (
                        <td className="py-2">
                          {store.login.roles.includes("VIEW_AND_MAINTAIN_USER_MANAGEMENT")?
                          <CButton
                            color="danger"
                            variant="outline"
                            shape="square"
                            size="sm"
                            onClick={

                              () => confirmDialog(item, "Delete")
                            }
                          >
                            Delete
                          </CButton>:""}
                        </td>
                      )
                    },
                  'details':
                    (item, index) => {
                      return (
                        <CCollapse show={details.includes(index)}>
                          <CCardBody>
                            <div className="d-flex justify-content-start">

                              <div style={{marginRight: 20, width: "40%"}}><h4 style={{marginBottom: 4}}>
                                <strong>{item.firstName + " " + item.lastName}</strong></h4>
                                <p>

                                  <p className="text-muted">Registered
                                    On: {new Date(parseFloat(item.createdOn)).toLocaleString()}</p>

                                </p>
                              </div>
                              {item.statusId == 2 ?
                                <div style={{marginRight: 20, width: "60%"}}><h6 style={{marginBottom: 4}}><strong>User
                                  Activation</strong></h6><p>
                                  User has not been activated yet.
                                  <CButton
                                    variant="outline"
                                    color="warning"
                                    shape="square"
                                    size="sm"
                                    style={{marginLeft: 10, marginRight: 10}}
                                  >
                                    Resend Activation number
                                  </CButton>
                                </p>
                                  <div style={{marginRight: 20, width: "60%"}}><h6 style={{marginBottom: 4}}><strong>Activation Number</strong></h6><p>
                                    {item.activationNo}
                                  </p></div>
                                </div>


                                : item.statusId == 1 ?
                                  <div style={{marginRight: 20, width: "60%"}}><h6 style={{marginBottom: 4}}><strong>User
                                    Activation</strong></h6><p>
                                    User account Activated.

                                    {store.login.roles.includes("VIEW_AND_MAINTAIN_USER_MANAGEMENT")?
                                    <CButton
                                      variant="outline"
                                      color="danger"
                                      shape="square"
                                      size="sm"
                                      style={{marginLeft: 10, marginRight: 10, paddingLeft: 10, paddingRight: 10}}
                                      onClick={

                                        () => confirmDialog(item, "Suspend")
                                      }
                                    > Suspend User</CButton>:""}

                                  </p></div> : item.statusId == 4 ?
                                    <div style={{marginRight: 20, width: "60%"}}><h6 style={{marginBottom: 4}}><strong>User
                                      Activation</strong></h6><p>
                                      User account Suspended.

                                      {store.login.roles.includes("VIEW_AND_MAINTAIN_USER_MANAGEMENT")?
                                        <CButton
                                          variant="outline"
                                          color="success"
                                          shape="square"
                                          size="sm"
                                          style={{marginLeft: 10, marginRight: 10}}
                                          onClick={

                                            () => confirmDialog(item, "Reactivate")
                                          }
                                        > Reactivate User</CButton>:""}

                                    </p></div> : ""}

                            </div>

                          </CCardBody>
                        </CCollapse>
                      )
                    }
                }}
              />
            </CCardBody>
          </CCard>
        </CCol>

      </CRow>
      <div>
        <Dialog
          open={dialogConfirm}
          TransitionComponent={Transition}
          keepMounted
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
        >
          <DialogTitle id="alert-dialog-slide-title">{<h5>Are You sure?</h5>}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              {<div><h5 style={{textAlign: 'center'}}><small>You are about
                to {alterAction} User <strong>{userTobeAltered.firstName + " " + userTobeAltered.lastName + " "}</strong> -
                Role <strong>{userTobeAltered.userRoleName}</strong></small></h5><h6 style={{textAlign: 'center'}}> Are
                You sure you want to proceed?</h6></div>}
            </DialogContentText>
          </DialogContent>
          <div className="d-flex justify-content-between">
            <button type="button" className="btn btn-outline-danger m-2 btn-sm" onClick={() => {
              setDialogConfirm(false)
            }}>Cancel
            </button>

            <button className="btn btn-outline-success m-2 btn-sm" type="submit" onClick={(e) => {
              handleAction(userTobeAltered.userId)
            }}>Continue
            </button>

          </div>
        </Dialog>
      </div>

    </>
  )
};

export default Users

